.profile__title{
    max-width: 294px;
    font-weight: 500;
    font-size: 42px;
    line-height: 48px;
    color: #ffffff;
    margin: 0;
    padding: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}
@media screen and (max-width: 690px) {
    .profile__title{
        max-width: 189px;
        font-size: 27px;
        line-height: 33px;
        text-align: center;
        margin-left: 45px;
    }
}